.c-post {
  &1 {
    width: 30%;
    &__title {
      font-size: 1.8rem;
      text-transform: uppercase;
      font-family: "Roboto", sans-serif;
      font-weight: 400;
      padding-top: 15px;
    }
    &__text {
      padding-top: 10px;
      font-size: 1.6rem;
      line-height: 1.5;
      color: #333333;
      font-family: "Roboto", sans-serif;
      font-weight: 300;
      text-align: justify;
    }
  }
  &2 {
    width: 532px;
    &__title {
      font-size: 1.31371518655vw;
      line-height: 1.2;
      font-family: "Roboto", sans-serif;
      font-weight: 400;
      padding-top: 20px;
    }
    &__txt {
      font-size: 1.051vw;
      line-height: 1.5;
      font-family: "Roboto", sans-serif;
      font-weight: 300;
      color: #7f7f7f;
      padding: 15px 0px;
    }
    &__address {
      font-size: 1.471361vw;
      line-height: 1.5;
      font-family: "Roboto", sans-serif;
      font-weight: 400;
      color: #f32129;
    }
  }
  &3 {
    width: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__title {
      font-size: 1.36626379401vw;
      line-height: 1.2;
      font-family: "Roboto", sans-serif;
      font-weight: 500;
      color: #2b2b2b;
      padding-top: 20px;
    }
    &__text {
      font-size: 1.05vw;
      line-height: 1.2;
      font-family: "Roboto", sans-serif;
      font-weight: 400;
      color: #2b2b2b;
      padding-top: 15px;
      text-align: center;
    }
    &__social {
      padding-top: 15px;
      ul {
        display: flex;
        justify-content: space-between;
        li {
          & + li {
            margin-left: 20px;
          }
          a {
            cursor: pointer;
          }
        }
      }
    }
  }
}
