/* Header
------------------------------------------------------------*/
.c-header {
  height: 175px;
  position: fixed;
  top: 0;
  width: 100%;
  background: rgba(255, 255, 255, 0.9);
  background: url("../img/header_pattern.png") no-repeat;
  z-index: 1000;
  &__inner {
    height: 100%;
    width: 100%;
    position: relative;
  }
  &__logo {
    position: absolute;
    left: 50%;
    top: 20px;
    transform: translateX(-50%);
    width: 15.0814503416vw;
  }
  &__cnt {
    width: 75%;
    margin: 0 auto;
    height: 100%;
    display: flex;
    justify-content: space-between;
  }
}
/* Main menu
------------------------------------------------------------*/
.c-gnav {
  padding-top: 50px;
  ul {
  }
  li {
    display: inline-block;
    + li {
      margin-left: 4.72937467vw;
    }
  }
  a {
    font-size: 1.05097214924vw;
    line-height: 1;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    color: #040607;
    text-transform: capitalize;
    @media only screen and (max-width: 1500px) {
      font-size: 1.8rem;
    }
    &.is-active {
      color: #e12027;
    }
  }
}
