/* layout
------------------------------------------------------------*/
body {
  font-family: $mainFonts;
  font-size: 1.4rem;
  min-width: $baseWidth;
  line-height: 1.5;
  @include breakpoint(sp) {
    min-width: 320px;
  }
}

//-------------------------------------------------------
.l-container {
  width: $baseWidth;
  margin: 0 auto;
}
//-------------------------------------------------------
.c-mainvisual {
  position: relative;
  overflow: hidden;
  &__img {
    position: absolute;
    bottom: -1px;
  }
  &__slide {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      -webkit-transition: linear 10s;
      transition: linear 10s;
      -webkit-transform: scale(1.5, 1.5);
      transform: scale(1.5, 1.5);
    }
  }
  .c-slide {
    &.is-moving {
      img {
        -webkit-transform: scale(1.2, 1.2);
        transform: scale(1.2, 1.2);
      }
    }
  }
  //==================
  &--subpage {
    height: 300px;
    h2 {
      font-size: 5rem;
    }
  }
}
