/*------------------------------------------------------------
btn
------------------------------------------------------------*/

/*
------------------------------------------------------------*/
.c-btn {
  &1 {
    display: flex;
    justify-content: center;
    a {
      width: 19.4429847609vw;
      height: 4.25643720441vw;
      background: #f32129;
      color: #ffffff;
      font-size: 2.1vw;
      font-weight: 600;
      font-family: "Roboto", sans-serif;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 10px;
      transition: all 0.5s;
      box-shadow: rgba(17, 17, 26, 0.1) 0px 8px 24px,
        rgba(17, 17, 26, 0.1) 0px 16px 56px, rgba(17, 17, 26, 0.1) 0px 24px 80px;
      &:hover {
        opacity: 1;
        background: #bd1016;
      }
    }
  }
  &2 {
    a {
      width: 19.4429847609vw;
      height: 4.25643720441vw;
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: "Roboto", sans-serif;
      font-size: 2.1vw;
      font-weight: 600;
      background: #f32129;
      color: #ffffff;
      border-radius: 10px;
      cursor: pointer;
      transition: all 0.5s;
      box-shadow: -1px -3px 70px 16px rgba(0, 0, 0, 1);
      -webkit-box-shadow: -1px -3px 70px 16px rgba(0, 0, 0, 1);
      -moz-box-shadow: -1px -3px 70px 16px rgba(0, 0, 0, 1);
      &:hover {
        opacity: 1;
        background: #bd1016;
      }
    }
  }
  &__top {
    position: fixed;
    bottom: 80px;
    right: 30px;
    width: 50px;
    height: 50px;
    opacity: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    background: #3e444a;
    border-radius: 3px;
    -webkit-transform: translateY(60px);
    transform: translateY(60px);
    -webkit-transition: all 1s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: all 1s cubic-bezier(0.645, 0.045, 0.355, 1);
    -webkit-transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
    transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
    z-index: 1000;
    i {
      font-size: 18px;
      line-height: inherit;
      display: inline-block;
      vertical-align: top;
      font-style: normal;
      color: #ffffff;
    }
    &--none {
      opacity: 1;
      -webkit-transform: translateY(0px);
      transform: translateY(0px);
    }
  }
}
