/*------------------------------------------------------------
Css of home page
------------------------------------------------------------*/
.p-top {
  //-------------------------------------------------------
  &1 {
    &__cnt {
      display: flex;
      padding-top: 60px;
    }
    &__left {
      width: 65%;
      display: flex;
      justify-content: space-between;
    }
    &__right {
      width: 35%;
      padding-left: 9.2vw;
      position: relative;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        margin-top: -5.25486vw;
      }
    }
  }
  //-------------------------------------------------------
  &2 {
    padding: 100px 0 95px;
    &__inner {
      padding-bottom: 80px;
    }
    &__cnt {
      display: flex;
      padding-top: 100px;
    }
    &__left {
      width: 55%;
      background: #f7feff;
      padding: 5.25486vw 4.7293746vw 3.94114vw;
      box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    }
    &__box {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    &__right {
      width: 45%;
      padding-top: 4.83447vw;
    }
    &__item {
      width: 30.75%;
      margin-bottom: 3.875%;
      a {
        &:hover {
          opacity: 1;
          img {
            -webkit-transform: scale(1.1);
            -ms-transform: scale(1.1);
            transform: scale(1.1);
          }
        }
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          transition: 0.7s ease-in-out;
        }
      }
    }
    &__image {
      padding-left: 7.567vw;
    }
    &__title {
      padding-left: 6vw;
      font-size: 1.31371518655vw;
      font-family: "Roboto", sans-serif;
      font-weight: 500;
    }
    &__text {
      padding-top: 20px;
      padding-left: 10vw;
      padding-right: 3vw;
      font-size: 1.31371518655vw;
      font-family: "Roboto", sans-serif;
      font-weight: 300;
    }
  }
  //-------------------------------------------------------
  &3 {
    padding-bottom: 50px;
    &__cnt {
      display: flex;
      justify-content: space-between;
      width: 1178px;
      margin: 0 auto;
    }
  }
  //-------------------------------------------------------
  &4 {
    height: 26.5895953757vw;
    background: url("../img/top4_image1.jpg") no-repeat;
    background-size: cover;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background: #000000;
      opacity: 0.6;
      z-index: 1;
    }
    &__inner {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      color: #ffffff;
      position: relative;
      z-index: 10;
      > p {
        font-size: 2.20704vw;
        line-height: 1.3;
        font-family: "Roboto", sans-serif;
        font-weight: 300;
        text-align: center;
      }
      > h2 {
        font-size: 2.52233315817vw;
        line-height: 1.5;
        font-family: "Roboto", sans-serif;
        font-weight: 500;
        text-align: center;
        margin-bottom: 40px;
        margin-top: 10px;
      }
    }
  }
  &5 {
    &__inner {
      display: flex;
      padding-top: 5.5vw;
      padding-bottom: 5.5vw;
    }
  }
  &6 {
    padding: 65px 0px 30px;
    background: #f9f9f9;
    &__inner {
      padding-top: 65px;
    }
    &__slide {
      display: flex;
      img {
        width: 20%;
      }
    }
  }
}
